eight: 90%;\n  border: none;\n  justify-self: end;\n  background-color: transparent;\n}\n.bar1,\n.bar2,\n.bar3 {\n  width: 100%;\n  height: calc(100% / 6);\n  background-color: #333;\n  margin: 6px 0;\n  transition: 0.4s;\n}\n\n.drawer {\n  padding: 5%;\n  display: none;\n  right: 0;\n  position: absolute;\n  background-color: white;\n  border: 4px solid grey;\n  border-top: none;\n  height: 100%;\n  width: 50%;\n}\n.drawerOnDisplay {\n  display: block;\n  animation: slideDrawer 0.3s forwards;\n}\n\n.drawer ul {\n  display: grid;\n  grid-template-rows: repeat(3, 30%);\n  list-style: none;\n  font-size: 3vmax;\n  height: 60%;\n  width: 100%;\n}\n.drawer li {\n  width: 100%;\n  height: fit-content;\n  border-bottom: 2px solid grey;\n  padding: 1%;\n}\n.drawer a {\n  display: flex;\n  background-color: white;\n  height: 44px;\n  width: 100%;\n}\n\n.navlink {\n  margin-block: auto;\n  position: relative;\n  display: grid;\n  height: 80%;\n  gap: 2%;\n  align-items: center;\n  justify-content: end;\n}\n\nnav h1 {\n  margin-block: auto;\n  height: fit-content;\n  font-size: 3vmax;\n}\n.navlink a {\n  padding: 0;\n  margin: 0;\n  display: none;\n  min-width: 44px;\n  min-height: 44px;\n  font-size: 3vmax;\n  align-items: center;\n  justify-content: center;\n}\nnav a:hover {\n  border-bottom: 2px solid black;\n}\nmain {\n  padding: 0;\n  width: 100%;\n  height: fit-content;\n  display: grid;\n  gap: 5%;\n  font-size: 3vmax;\n}\nmain h1 {\n  width: 100%;\n  justify-self: center;\n}\narticle {\n  width: 100%;\n  height: fit-content;\n}\n.list-container {\n  align-items: center;\n  text-align: center;\n  justify-content: center;\n  gap: 5px;\n  font-size: 1.5vmax;\n  height: 120vh;\n  overflow: scroll;\n  margin: 0;\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n}\n.list-container li {\n  font-size: 1vmax;\n}\n.custom-Card {\n  background-color: rgb(255, 255, 255);\n  width: 98%;\n  height: 98%;\n  padding: 2px;\n  object-fit: fill;\n  cursor: pointer;\n  font-size: 1.5vmax;\n  display: flex;\n  font-family: \"Inria Serif\", serif;\n  justify-content: center;\n  justify-self: center;\n  border: 1px solid black;\n}\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: rgb(246, 4, 4);\n\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n/* page detail css */\n.detail-page {\n  width: 100%;\n  height: 180vh;\n  display: grid;\n  grid-template-columns: 1fr;\n  grid-template-rows: 5% 45% 50%;\n}\n.desc-container {\n  font-size: 2vmax;\n  border: 2px solid grey;\n  list-style: none;\n  display: grid;\n  justify-items: center;\n  align-items: center;\n  width: 100%;\n  height: 100%;\n}\n.desc-container div {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  height: 100%;\n  width: 100%;\n}\n.detail-page h1 {\n  text-align: center;\n  margin: auto;\n  font-size: 4vmax;\n}\n.desc-container img {\n  width: 100%;\n  height: 100%;\n}\n.desc-container ul {\n  align-content: center;\n  width: 95%;\n  height: 90%;\n  display: grid;\n  grid-template-rows: 50% 7% 7% 36%;\n  list-style: none;\n}\n.desc-container li {\n  height: 100%;\n}\n\n.desc-container li p {\n  height: 90%;\n}\n\n.desc-container ul li:nth-child(n + 2):nth-child(-n + 4) {\n  font-weight: bold;\n  font-size: 2vmax;\n  padding: 5px;\n}\n\n.desc-container span,\n.desc-container p {\n  font-weight: 200;\n  overflow-y: auto;\n}\n.other-container {\n  gap: 5%;\n  border: 2px solid grey;\n  width: 100%;\n  height: 100%;\n  display: grid;\n  grid-template-rows: 50% 40%;\n  justify-items: center;\n}\n.menu {\n  height: 100%;\n  width: 95%;\n  display: grid;\n  justify-content: space-around;\n  grid-template-columns: 1fr 1fr;\n  grid-template-rows: 20% 80%;\n  gap: 10px;\n}\n.menu h2 {\n  font-size: 4vmax;\n  padding-inline-start: 2%;\n  height: fit-content;\n  justify-self: center;\n  align-self: center;\n  grid-column-start: 1;\n  grid-column-end: 3;\n}\n.menu div {\n  border: 2px solid grey;\n  display: flex;\n  flex-direction: column;\n  font-size: 2vmax;\n  list-style: none;\n  width: 100%;\n  height: 100%;\n  align-items: center;\n}\n.menu div ul {\n  overflow-y: auto;\n  height: 90%;\n  width: 90%;\n  display: flex;\n  flex-direction: column;\n  list-style: none;\n  align-items: center;\n}\n\n.menu h2 {\n  margin: 10px;\n  text-align: center;\n  font-size: 3vmax;\n}\n.menu li {\n  padding: 2px;\n  justify-self: center;\n  border-bottom: 2px dashed grey;\n  width: 90%;\n}\n.review-container {\n  width: 90%;\n  height: 100%;\n}\n.review {\n  display: flex;\n  justify-content: center;\n  width: 100%;\n  font-size: 3vmax;\n  height: 85%;\n  border: 2px solid grey;\n  display: block;\n  overflow: hidden;\n}\n.review-container h2 {\n  margin: auto;\n  width: 100%;\n  display: grid;\n  justify-self: center;\n  align-self: center;\n  grid-template-columns: 1fr 1fr;\n  font-size: 3vmax;\n}\n.review-container h2 span {\n  width: 100%;\n  text-align: end;\n}\n.review {\n  margin: auto;\n  height: 80%;\n  overflow: scroll;\n  width: 100%;\n}\n.review li {\n  border-bottom: 1px solid gray;\n  font-size: 1.5vmax;\n  width: 90%;\n  padding-inline: 5%;\n  height: 30%;\n  margin-block: 5px;\n}\n.review h3 {\n  height: 40%;\n  width: 100%;\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n}\n\n.review h3 span {\n  font-size: 1.5vmax;\n  text-align: end;\n  color: gray;\n}\n\nfooter {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  text-align: center;\n  background-color: #333;\n  height: 20vh;\n  width: 100%;\n  font-size: 2vmax;\n  color: white;\n}\n\n@media screen and (min-width: 450px) {\n  .customCard {\n    height: 100%;\n  }\n  .detail-page {\n    height: 170vh;\n    justify-content: center;\n  }\n  .desc-container,\n  .other-container {\n    margin: auto;\n    width: 90%;\n  }\n  footer {\n    font-size: 4vmax;\n  }\n  article {\n    width: 100%;\n    gap: 5px;\n    grid-template-columns: repeat(3, 1fr);\n  }\n}\n\n@media screen and (min-width: 700px) {\n  .container {\n    display: none;\n  }\n  .navlink {\n    grid-template-columns: repeat(3, 1fr);\n  }\n  .navlink a {\n    align-items: center;\n    display: flex;\n  }\n}\n\n@media screen and (min-width: 800px) {\n  .navlink a {\n    font-size: 2vmax;\n  }\n  .customCard {\n    border: 2px solid grey;\n    font-size: 1vmax;\n  }\n  main {\n    height: fit-content;\n  }\n  .detail-page {\n    height: 150vh;\n  }\n  .desc-container,\n  .other-container {\n    width: 100%;\n  }\n  footer {\n    font-size: 2vmax;\n  }\n\n  article {\n    font-size: 1vmin;\n    grid-template-columns: repeat(4, 1fr);\n  }\n}\n@media screen and (min-width: 1000px) {\n  article {\n    padding-block: 2vh;\n    overflow: hidden;\n    border: 2px solid grey;\n    width: 100%;\n    height: fit-content;\n  }\n  .list-container {\n    grid-template-columns: repeat(4, 1fr);\n  }\n  .detail-page {\n    grid-template-columns: 50% 50%;\n    grid-template-rows: 10% 90%;\n    height: 150vh;\n  }\n  .detail-page h1 {\n    grid-column-start: 1;\n    grid-column-end: 3;\n  }\n}\n\n@media screen and (min-width: 1000px) and (min-height: 1300px) {\n  .detail-page {\n    grid-template-columns: 1fr;\n    grid-template-rows: 5% 45% 50%;\n    height: 170vh;\n  }\n  .detail-page h1 {\n    grid-column-start: 1;\n    grid-column-end: 2;\n  }\n}\n"],"sourceRoot":""}]);
